<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-order" id="pos-list">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableDate" border style="width:100%;">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" width="80" label="数量"></el-table-column>
              <el-table-column prop="price" width="80" label="金额"></el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                <template slot-scope="scope"> 
                  <el-button type="text" size="small" @click="deleteSingel(scope.row)">删除</el-button>
                  <el-button type="text" size="small" @click="addaddOrderList(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="totalDiv">
              <small>数量</small>{{totalCount}}&nbsp;&nbsp;&nbsp;&nbsp; <small>金额</small>{{totalMoney}}元
            </div>
            <div class="select-btn">
              <el-button type='warning'>挂单</el-button>
              <el-button type='danger' @click="deleteAllSingel">删除</el-button>
              <el-button type='success' @click="settleAccounts">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">

          </el-tab-pane>
          <el-tab-pane label="外卖">

          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span='17'>
        <div class="hot-goods">
          <div class="title">热卖商品</div>
          <div class="hot-goods-list">
            <ul>
              <li v-for="goods in oftenGoods" :key="goods.goodsId" @click="addaddOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="goods-style">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div>
                <ul class='cookList'>
                    <li v-for="goods in type0Goods" :key="goods.id" @click="addaddOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <div>
                <ul class='cookList'>
                    <li v-for="goods in type1Goods" :key="goods.id" @click="addaddOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div>
                <ul class='cookList'>
                    <li v-for="goods in type2Goods" :key="goods.id" @click="addaddOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div>
                <ul class='cookList'>
                    <li v-for="goods in type3Goods" :key="goods.id" @click="addaddOrderList(goods)">
                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                        <span class="foodName">{{goods.goodsName}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import axios from 'axios'
export default {
  name: 'pos',
  data(){
    return{
      tableDate:[],
      oftenGoods:[],
      type0Goods:[],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalCount:0,
      totalMoney:0,
      deleteChange:false
    }
  },
  created(){
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
    .then(response=>{
      //console.log(response)
      this.oftenGoods=response.data;
    })
    .catch(error=>{
      //console.log(error);
      alert('网络错误，不能访问');
    })

    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
      .then(response=>{
         console.log(response);
         this.type0Goods=response.data[0];
         this.type1Goods=response.data[1];
         this.type2Goods=response.data[2];
         this.type3Goods=response.data[3];
      })
      .catch(error=>{
          console.log(error);
          alert('网络错误，不能访问');
      })
  },
  mounted(){
    var orderHeigth=document.body.clientHeight;
    document.getElementById('pos-list').style.height=orderHeigth+'px';
  },
  methods:{
    //添加订单列表方法
    addaddOrderList(goods){
      // this.totalCount=0;
      // this.totalMoney=0;
      let isHave=false;
      for(let i=0;i<this.tableDate.length;i++){
        if(this.tableDate[i].goodsId== goods.goodsId){
          isHave=true;
        }
      }
      if(isHave){
         let arr = this.tableDate.filter(o =>o.goodsId == goods.goodsId);
         arr[0].count++;
      }else{
        let newTableDate={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
        this.tableDate.push(newTableDate);
      }
      this.getAllMoney();
    },

    deleteSingel(goods){
      this.tableDate=this.tableDate.filter(o=>o.goodsId!=goods.goodsId);
      this.getAllMoney();
    },
    
    //数量和金额的自动计算
    getAllMoney(){
      this.totalCount=0;
      this.totalMoney=0;
      this.tableDate.forEach((element) => {
        this.totalCount+=element.count;
        this.totalMoney=this.totalMoney+(element.price*element.count);   
      });
    },
    //删除操作
    deleteAllSingel(){
      this.tableDate=[];
      this.totalCount=0;
      this.totalMoney=0;
      if(!this.deleteChange){
        this.$message({
          message:'删除成功！',
          type:'success'
        })
        this.deleteChange=true;
      }else{
        this.$message({
          message:'已经删除，请点餐!',
          type:'danger'
        })
      }

    },

    //结账操作
    settleAccounts(){
      if(this.tableDate.length!=0){
        this.$message({
          message:'你好，一共'+this.totalMoney+'元',
          type:'success'
        })
         this.deleteAllSingel()
      }else{
        this.$message({
          message:'请点餐',
          type:'warning'
        })
      }
    }

  }
  
}
</script>


<style scoped>
  .pos-order{
    background-color:#F9FAFC;
    border-right:1px solid #C0CCDA;
  }
  .select-btn{
    margin-top:10px;
  }
  .title{
    text-align:left;
    padding:10px;
    border-bottom:1px solid #D3DCE6;
    background-color: #F9FAFC;
  }
  .hot-goods-list ul li{
    list-style:none;
    float: left;
    border:1px solid #E5E9F2;
    padding:10px;
    margin:5px;
    background-color:#fff;
    cursor: pointer;
  }
  .o-price{
    color:#58B7FF;
  }
  .goods-style{
    clear: both;
  }
  .cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 14px;
       padding-left: 10px;
       color:brown;
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .totalDiv{
     background-color:#fff;
     padding:10px;
     border-bottom:1px solid #D3DCE6;
   }
</style>



